.roll {
  position: absolute;
  top: 0px;
  z-index: -100;
}

.scroll-text-row {
  margin: 0%;
  padding: 0.5em 10em;
  width: 240%;
  background-color: #e6a28b;
  display: flex;
  flex-wrap: nowrap;
  -webkit-animation: move 40s infinite linear;
          animation: move 40s infinite linear;
  justify-content: space-around;
  overflow: hidden;
}

@-webkit-keyframes move {
  to {
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
}

@keyframes move {
  to {
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
}

.scroll-text {
  margin: 0;
  padding-left: 0px;
  font-family: Aurebesh;
  letter-spacing: 2px;
}

.scroll-text-div {
  margin: 0;
  padding-left: 0px;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: poppins, sans-serif;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 0.025em;
  line-height: 1.8em;
}

html,
body {
  background-color: #F5E2CD;
  overflow-x: hidden;
}

.App {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  text-align: center;
}

h1 {
  font-family: poppins, sans-serif;
  text-transform: none;
  color: #DF7653;
  font-size: 28px;
  font-weight: 700;
  letter-spacing: 0px;
  line-height: 1.4em;
  margin-bottom: -10px;
  text-transform: lowercase;
}

h2 {
  font-family: BeachesAndCream;
  letter-spacing: -1px;
