  font-size: 50px;
}

vday {
  color: #e6a28b;
  font-weight: 800;
  border-bottom: 2px solid #e6a28b;
}

#pictures,
#stats,
#coming-soon {
  padding: 10vh 0;
  background-color: #F8EBD8;
}

.paragraph {
  width: 500px;
  padding-top: 30px;
  margin: auto;
}

/* highlight text */

::selection {
  color: #F9EFE0;
  background: #DF7653;
}
.navbar {
  width: 100%;
  height: 60px;
  display: flex;
  flex-direction: row;
}

.navbar .linkies {
  flex: 100% 1;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.navbar a {
  color: #403029;
  text-decoration: none;
  margin: 40px;
  font-size: 14px;
  letter-spacing: 1px;
  font-family: poppins, sans serif;
  text-transform: lowercase;
  font-weight: 600;
  display: inline-block;
  position: relative;
}

.navbar a::after {
  content: '';
  position: absolute;
  width: 100%;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #E6A28B;
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}

.navbar a:hover::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
}
.footer {
  background-color: #e6a28b;
  padding-top: 5vh;
  padding-bottom: 2vh;
  color: #fcf4e8;
  font-weight: 400;
  font-size: 14px;
}

.footer h2 {
  font-family: Aurebesh;
  color: #fcf4e8;
  letter-spacing: 3px;
  font-size: 30px;
}

#content {
  padding: 30px 0;
  width: 800px;
  margin: auto;
  display: flex;
  align-content: flex-start;
}

.footer-1 {
  text-align: middle;
  padding: 0 60px;
}

.footer-2 {
  text-align: left;
  padding: 0px 100px 0px 0px;
}

.footer-3 {
  text-align: left;
  font-size: 14px;
}

.footer .linkies {
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  flex-direction: column;
}

.footer a {
  color: #fcf4e8;
