    display: flex;
    flex-direction: row;
    justify-content: center;
    padding-top: 50px;
}

#moments .flex-container #column {
    width: 500px;
    margin: 15px;
    border-radius: 20px;
    padding: 15px;
}

#moments .flex-container #column img {
    width: 450px;
}

.flex-container #column h1 {
    color: #e6a28b;
    font-family: poppins, sans-serif;
    font-size: 80px;
    font-weight: 400;
    line-height: 2em;
}

@media screen and (max-width: 1440px) {
    #moments {
        padding: 10vh 10vw;
    }

    #moments .flex-container #column {
        width: 450px;
        margin: 15px;
        border-radius: 20px;
        padding: 15px;
    }

    #moments .flex-container #column img {
        width: 400px;
    }

    h2 {
        font-size: 40px;
    }
}
